/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 800px;
}

.no-webp .home {
  background-image: url(../img/header.jpg);
}

.webp .home {
  background-image: url(../img/header.jpg);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.home {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.home .header-container {
    min-height: 70rem;
  }
}

.header-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-container h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
}

.footer {
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  padding: 2.5rem 0;
  margin-bottom: 0;
}

.bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navigation {
    display: flex;
    gap: 2rem;
  }
}
.navigation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}

.icons-us {
  display: grid;
  gap: 2 rem;
  grid-template-columns: repeat(3, 1fr);
}

.icon {
  text-align: center;
}
.icon img {
  display: inline;
  height: 10rem;
  width: auto;
}
.icon h3 {
  text-transform: uppercase;
}